(function (global, factory) {
  typeof exports === 'object' && typeof module !== 'undefined' ? module.exports = factory(require('superagent-bluebird-promise')) :
  typeof define === 'function' && define.amd ? define(['superagent-bluebird-promise'], factory) :
  global.imgur = factory(global.request)
}(this, function (request) { 'use strict';

  var utils = {
      API_URL: 'https://api.imgur.com',
      API_VERSION: '3',
      CLIENT_ID: '',
      buildOptions: (apiUrl, path, method, body = {}) => {
          return { apiUrl, path, method, body };
      },
      bearer: ''
  };

  var imgurAPICall = function(options) {
      ['method', 'apiUrl', 'path', 'body'].forEach(option => {
          if (!options[option]) {
              throw new Error(`${option} must be specified`);
          }
      });

      let authToken = `Client-ID ${utils.CLIENT_ID}`;

      if(utils.bearer) {
          authToken = `Bearer ${utils.bearer}`;
      }

      return request[options.method](`${options.apiUrl}/${options.path}`)
          .send(options.body)
          .set('Authorization', authToken)
          .promise();
  };

  var endpoint = function(options) {
      options.imgurAPICall = imgurAPICall.bind(options);
      options.apiUrl = options.apiUrl || `${utils.API_URL}/${utils.API_VERSION}`;

      return options;
  }

  var imageEndpoint = endpoint({
      path: 'image',
      apiUrl: `${utils.API_URL}/${utils.API_VERSION}`,
      get(hash) {
          const options = utils.buildOptions(this.apiUrl, `${this.path}/${hash}`, 'get');

          return this.imgurAPICall(options);
      }
  });

  var albumEndpoint = endpoint({
      path: 'album',
      apiUrl: `${utils.API_URL}/${utils.API_VERSION}`,
      get(hash) {
          const options = utils.buildOptions(this.apiUrl, `${this.path}/${hash}`, 'get');

          return this.imgurAPICall(options);
      }
  });

  var oauth2Endpoint = endpoint({
      path: 'oauth2',
      apiUrl: utils.API_URL,
      get(responseType) {
          const resType = responseType || 'token';
          const queryString = '?' + [
              `response_type=${resType}`,
              `client_id=${utils.CLIENT_ID}`
        ].join('&');

          const path = `${this.path}/authorize${queryString}`;
          const options = utils.buildOptions(this.apiUrl, path, 'get');

          return this.imgurAPICall(options);
      },
      refresh(refreshToken, clientSecret) {
          const queryString = '?' + [
              `refresh_token=${refreshToken}`,
              `client_id=${utils.CLIENT_ID}`,
              `client_secret=${clientSecret}`,
              'grant_type=refresh_token'
          ].join('&');

          const path = `${this.path}/token${queryString}`;
          const options = utils.buildOptions(this.apiUrl, path, 'post');

          return this.imgurAPICall(options);
      }
  });

  var topicsEndpoint = endpoint({
      path: 'topics',
      apiUrl: `${utils.API_URL}/${utils.API_VERSION}`,
      get(topicId, sort='viral', page=0) {
          const requestPath = `${this.path}/${topicId}/${sort}/${page}`;
          const options = utils.buildOptions(this.apiUrl, requestPath, 'get');

          return this.imgurAPICall(options);
      },
      getDefaults() {
          const requestPath = `${this.path}/defaults`;
          const options = utils.buildOptions(this.apiUrl, requestPath, 'get');

          return this.imgurAPICall(options);
      }
  });

  const postOptions = {
      path: 'gallery',
      apiUrl: `${utils.API_URL}/${utils.API_VERSION}`
};

  function extend() {
    var key, obj, result = {}, i;
    for (i = 0; i <= arguments.length; i++) {
      obj = arguments[i];
      for (key in obj) {
        if (obj.hasOwnProperty(key)) {
          result[key] = obj[key];
        } else {
        }
      }
    }
    return result;
  }

  var galleryPostEndpoint = endpoint(extend({}, postOptions, {
      REASON_DOES_NOT_BELONG_ON_IMGUR: 1,
      get(hash) {
          const path = `${this.path}/${hash}`;
          const options = utils.buildOptions(this.apiUrl, path, 'get');

          return this.imgurAPICall(options);
      },
      report(hash, reason = this.REASON_DOES_NOT_BELONG_ON_IMGUR) {
          if(!hash) { throw new Error('hash must be specified'); }

          if(typeof reason !== 'number') {
              throw new Error('the reason must be an integer');
          }

          const path = `${this.path}/${hash}/report`;
          const options = utils.buildOptions(this.apiUrl, path, 'post', { reason });

          return this.imgurAPICall(options);
      },
      _handleVote(hash, voteType) {
          if(!hash) { throw new Error('hash must be specified'); }

          const path = `${this.path}/${hash}/vote/${voteType}`;
          const options = utils.buildOptions(this.apiUrl, path, 'post');

          return this.imgurAPICall(options);
      },
      upvote(hash) {
          return this._handleVote(hash, 'up');
      },
      downvote(hash) {
          return this._handleVote(hash, 'down');
      },
      favorite(hash, isAlbum) {
          if(!hash) { throw new Error('hash must be specified'); }

          if(isAlbum === undefined || typeof(isAlbum) !== 'boolean') {
              throw new Error('isAlbum with type boolean must be specified');
          }

          const postType = isAlbum ? 'album' : 'image';
          //doesn't use gallery path because it could be a non gallery item
          const path = `${postType}/${hash}/favorite`;
          const options =  utils.buildOptions(this.apiUrl, path, 'post');

          return this.imgurAPICall(options);
      },
      comments: endpoint(extend({}, postOptions, {
          get(hash, sort='best') {
              const path = `${this.path}/${hash}/comments/${sort}`;
              const options = utils.buildOptions(this.apiUrl, path, 'get');

              return this.imgurAPICall(options);
          }
      }))
  }));

  var galleryEndpoint = endpoint({
      path: 'gallery',
      apiUrl: `${utils.API_URL}/${utils.API_VERSION}`,
      get(section='hot', sort='viral', page=0, showViral=true) {
          const requestPath = `${this.path}/${section}/${sort}/${page}?showViral=${showViral}`;
          const options = utils.buildOptions(this.apiUrl, requestPath, 'get');

          return this.imgurAPICall(options);
      },
      post: galleryPostEndpoint
  });

  var commentEndpoint = endpoint({
      path: 'comment',
      apiUrl: `${utils.API_URL}/${utils.API_VERSION}`,
      REASON_DOES_NOT_BELONG_ON_IMGUR: 1,
      get(commentId) {
          if(!commentId) { throw new Error('commentId must be specified'); }

          const path = `${this.path}/${commentId}`;
          const options = utils.buildOptions(this.apiUrl, path, 'get');

          return this.imgurAPICall(options);
      },
      downvote(commentId) {
          if(!commentId) { throw new Error('commentId must be specified'); }

          const path = `${this.path}/${commentId}/vote/down`;
          const options = utils.buildOptions(this.apiUrl, path, 'post');

          return this.imgurAPICall(options);
      },
      upvote(commentId) {
          if(!commentId) { throw new Error('commentId must be specified'); }

          const path = `${this.path}/${commentId}/vote/up`;
          const options = utils.buildOptions(this.apiUrl, path, 'post');

          return this.imgurAPICall(options);
      },
      report(commentId, reason = this.REASON_DOES_NOT_BELONG_ON_IMGUR) {
          if(!commentId) { throw new Error('commentId must be specified'); }

          if(typeof reason !== 'number') {
              throw new Error('the reason must be an integer');
          }

          const path = `${this.path}/${commentId}/report`;
          const options = utils.buildOptions(this.apiUrl, path, 'post', { reason });

          return this.imgurAPICall(options);
      },
      deleteComment(commentId) {
          if(!commentId) { throw new Error('commentId must be specified'); }

          const path = `${this.path}/${commentId}`;
          const options = utils.buildOptions(this.apiUrl, path, 'del');

          return this.imgurAPICall(options);
      },
      submitComment(params) {
          ['image_id', 'comment'].forEach(option => {
              console.log(!params[option]);
              if (!params[option]) {
                  throw new Error(`${option} must be specified`);
              }
          });

          const options = utils.buildOptions(this.apiUrl, this.path, 'post', params);
          return this.imgurAPICall(options);
      },
      submitReply(params) {
          ['image_id', 'comment', 'parent_id'].forEach(option => {
              if (!params[option]) {
                  throw new Error(`${option} must be specified`);
              }
          });

          const options = utils.buildOptions(this.apiUrl, this.path, 'post', params);
          return this.imgurAPICall(options);
      }
  });

  var imgur = function(clientKey, bearerKey) {
      let setUtil = function(key, value) {
          utils[key] = value;
      };

      let getUtil = function(key) {
          return utils[key];
      };

      if (!clientKey) {
          throw new Error('Client Key required to initialize imgur client');
      }

      setUtil('CLIENT_ID', clientKey);

      if(bearerKey) {
          setUtil('bearer', bearerKey);
      }

      return {
          imgurAPICall,
          CLIENT_ID: clientKey,
          image: imageEndpoint,
          album: albumEndpoint,
          oauth2: oauth2Endpoint,
          topics: topicsEndpoint,
          gallery: galleryEndpoint,
          comment: commentEndpoint,
          setUtil,
          getUtil
      };
  }

  return imgur;

}));
